export default (currArticle, allArticles) => {
  let { relatedProjects } = currArticle;
  let firstArticleId = false;
  // currently only takes into account discipline and categories separtely instead of taking both into account for match score

  // check for matching disciplines first
  if (relatedProjects.length !== 2) {
    getMoreRelatedArticles(relatedProjects, currArticle, allArticles, "disciplines");
  }

  // then matching categories --- currently not using categories
  // if(relatedProjects.length !== 2){
  //   getMoreRelatedArticles(relatedProjects, currArticle, allArticles, "categories");
  // }

  // if still not found 2, add most recent
  if (relatedProjects.length !== 2) {
    for (let i = 0; i < allArticles.length; i++) {
      if (relatedProjects.length !== 2) {
        if (allArticles[i].id !== currArticle.id && allArticles[i].id !== firstArticleId) {
          if (relatedProjects.length === 1 && allArticles[i].id !== relatedProjects[0].id) {
            relatedProjects.push(allArticles[i]);
          } else {
            relatedProjects.push(allArticles[i]);
          }
        }
      } else {
        break;
      }
    }
  }

  return relatedProjects;
};

const getMoreRelatedArticles = (relatedProjects, currArticle, allArticles, tag) => {
  let thisTag = currArticle[tag];
  let count = [];
  let projectTags = [];
  let firstArticleId = false;

  if (relatedProjects.length === 1) {
    firstArticleId = relatedProjects[0].id;
  }

  for (let i = 0; i < allArticles.length; i++) {
    if (allArticles[i].id !== currArticle.id && allArticles[i].id !== firstArticleId) {
      projectTags.push(allArticles[i][tag]);
    } else {
      projectTags.push([]);
    }
  }

  // feel like this could be slimmed down
  // maybe only push to array based on curr article tags first

  // loop through curr project disciplines
  for (let i = 0; i < thisTag.length; i++) {
    // loop through all other projects
    for (let j = 0; j < projectTags.length; j++) {
      if (!count[j]) {
        count[j] = {
          i: j,
          val: 0,
        };
      }

      // loop through other project's disciplines
      for (let k = 0; k < projectTags[j].length; k++) {
        if (projectTags[j][k] && thisTag[i].title === projectTags[j][k].title) {
          count[j].val++;
        }
      }
    }
  }

  count.sort((a, b) => {
    return b.val - a.val;
  });

  for (let i = 0; i < count.length; i++) {
    if (relatedProjects.length === 2) {
      break;
    }

    // if at least one matching tag found
    if (count[i].val >= 1) {
      relatedProjects.push(allArticles[count[i].i]);
    }
  }
};
