import React from "react";
import { graphql } from "gatsby";

import Paragraph from "./paragraph";
import Image from "./image";
import Vimeo from "./vimeo";

// TODO still needs video and image sequence
export default ({ moduleData }) => (
  <>
    {moduleData.map((item, i) => {
      if (item.moduleText && item.moduleText.length > 0) {
        return <Paragraph key={`module-${i}`} text={item.moduleText} />;
      }

      if (item.moduleImage && item.moduleImage.length > 0) {
        return <Image key={`module-${i}`} img={item.moduleImage[0]} />;
      }

      if (
        (item.moduleVimeoAmbientBackground && item.moduleVimeoAmbientBackground.length > 0) ||
        (item.moduleVimeoStory && item.moduleVimeoStory.length > 0)
      ) {
        return <Vimeo key={`module-${i}`} item={item} />;
      }

      return null;
    })}
  </>
);

export const query = graphql`
  fragment ModuleData on Craft_ModulesUnion {
    ... on Craft_ModulesModuleText {
      moduleText
    }
    ... on Craft_ModulesModuleImage {
      moduleImage {
        id
        dateUpdated
        url

        ... on Craft_UploadsVolume {
          __typename
          imageFile {
            childImageSharp {
              fluid(maxWidth: 1100, quality: 90) {
                src
                sizes
                srcSet
                srcWebp
                srcSetWebp
                aspectRatio
              }
            }

            colors {
              muted
            }
          }
        }
      }
    }
    ... on Craft_ModulesModuleVimeoAmbientBackground {
      moduleVimeoAmbientBackground
    }
    ... on Craft_ModulesModuleVimeoStories {
      moduleVimeoStory
      soundButtonColour
    }
    ... on Craft_ModulesModuleVideoUpload {
      moduleVideoUpload {
        url
        width
        height
      }
    }
    ... on Craft_ModulesModuleImageSequence {
      moduleImageSequence {
        id
        dateUpdated
        url

        ... on Craft_UploadsVolume {
          __typename
          imageFile {
            childImageSharp {
              fluid(maxWidth: 1100) {
                src
                sizes
                srcSet
                srcWebp
                srcSetWebp
                aspectRatio
              }
            }

            colors {
              muted
            }
          }
        }
      }
    }
  }
`;
