import React from "react";
import { graphql } from "gatsby";

import Layout from "../components/common/layout";

import Hero from "../components/article/hero";
import Heading from "../components/article/heading";
import Modules from "../components/article/articleModules";
import WhatWeDid from "../components/article/whatWeDid";
import RelatedArticles from "../components/article/relatedArticles";

export default ({ data }) => {
  const entry = data.craft.entries[0];

  return (
    <Layout className="article" seo={entry.seomatic}>
      <Hero heroImage={entry.image[0]} heroBannerVideo={entry.heroBannerVideo} />

      <div className="article__contain">
        <Heading title={entry.projectName} text={entry.heroTitle} />

        <Modules moduleData={entry.modules} />

        <WhatWeDid {...entry} />
      </div>

      <RelatedArticles currArticle={entry} />
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    craft {
      entries(section: [projects], slug: $slug) {
        ... on Craft_Projects {
          seomatic {
            metaTagContainer
            metaLinkContainer
            metaTitleContainer
            metaScriptContainer
            metaJsonLdContainer
          }
          hidden
          id
          slug
          heroTitle
          projectName
          video {
            url
            width
            height
          }
          heroBannerVideo
          image {
            id
            dateUpdated
            url

            ... on Craft_UploadsVolume {
              __typename
              imageFile {
                childImageSharp {
                  fluid(maxWidth: 1920) {
                    src
                    sizes
                    srcSet
                    srcWebp
                    srcSetWebp
                    aspectRatio
                  }
                }

                colors {
                  muted
                }
              }
            }
          }

          modules {
            ...ModuleData
          }

          categories {
            title
          }
          disciplines {
            title
          }
          externalLink

          relatedProjects(hidden: false) {
            ... on Craft_Projects {
              id
              slug
              image {
                id
                dateUpdated
                url

                ... on Craft_UploadsVolume {
                  __typename
                  imageFile {
                    childImageSharp {
                      fluid(maxWidth: 600) {
                        src
                        sizes
                        srcSet
                        srcWebp
                        srcSetWebp
                        aspectRatio
                      }
                    }

                    colors {
                      muted
                    }
                  }
                }
              }
              disciplines {
                title
              }
              categories {
                title
              }
            }
          }
        }
      }
    }
  }
`;
