import { TweenLite } from "gsap";

export const relatedArticlesTransition = (articleTransitionEl, direction, e) => {
  return new Promise(resolve => {
    const target = e.target;
    const tileDimensions = target.getBoundingClientRect();
    const heroEl = document.querySelector(".article__hero");
    const heroDimensions = heroEl.getBoundingClientRect();
    const scale = heroDimensions.width / tileDimensions.width;
    const transitionElImg = articleTransitionEl.current.querySelector(
      ".article__movingOverlay-image"
    );

    let moveTo = 0;

    if (heroDimensions.width < 768) {
      moveTo = window.pageYOffset + heroDimensions.top;
    }

    let imgSrc = target.src;

    if (imgSrc === undefined) {
      const clicked = document.querySelector(`.article__related-option.-${direction}`);

      imgSrc = clicked.querySelector("img").src;
    }

    const copyTileStyle = {
      top: tileDimensions.y + "px",
      left: tileDimensions.x + "px",
      width: tileDimensions.width + "px",
      height: tileDimensions.height + "px",
      opacity: 1,
    };

    TweenLite.to(articleTransitionEl.current, 0, { ...copyTileStyle });

    transitionElImg.style.backgroundImage = `url(${imgSrc})`;
    document.querySelector(".article").classList.add("-fade");

    const growToHero = {
      left: heroDimensions.x + "px",
      height: heroDimensions.height / scale + "px",
      scale: scale,
      ease: "Circ.easeOut",
    };

    TweenLite.to(articleTransitionEl.current, 0.4, {
      ...growToHero,
      onComplete: () => {
        window.scrollTo(0, 0);

        transitionElImg.style.filter = "blur(3px)";
        TweenLite.to(articleTransitionEl.current, 0.6, {
          top: moveTo + "px",
          ease: "Circ.easeOut",
          onComplete: () => {
            resolve();
          },
        });
      },
    });
  });
};

export const newArticleFadeIn = (articleTransitionEl, node) => {
  const articleEl = node.querySelector(".article");

  node.querySelector("nav").classList.add("-transitionHide"); // so hero img begin at top of screen
  articleEl.classList.add("-articleTransition");

  setTimeout(() => {
    // give FF 50ms to add hero img
    const heroImg = articleEl.querySelector(".article__hero-media").querySelector("img");
    const styles = articleTransitionEl.current.getAttribute("style");
    const newTransitionEl = articleEl.querySelector(".article__movingOverlay");

    newTransitionEl.setAttribute("style", styles);
    articleTransitionEl.current.style.opacity = 0;

    const heroImgLoaded = () => {
      // TODO - hold till image loaded, remove bcgColor for transition?
      newTransitionEl.style.opacity = 0;
      setTimeout(newTransitionEl.setAttribute("style", ""), 500);
    };

    if (heroImg.complete) {
      heroImgLoaded();
    } else {
      heroImg.addEventListener("load", () => {
        heroImgLoaded();
      });
    }
  }, 50);
};
